:root{
    --page-inline-padding: clamp(1rem, 1rem + 1.5vw, 2em);
    --page-block-shadow: 5px 5px 6px rgba(0,0,0,.5), -5px 5px 6px rgba(0,0,0,.5);
}

*,
::after,
::before {
    box-sizing: border-box
}

html {
    font-family: sans-serif;
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -ms-overflow-style: scrollbar;
    -webkit-tap-highlight-color: transparent;
    min-height: 100vh;
    overflow-y: scroll;
}

hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible
}

blockquote,
dl,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
p,
ul {
    margin: 0 0 1rem
}

h2{
    margin-bottom: .5em;
    line-height: 1.2;
    display: inline-block;
    border-bottom:1px solid rgba(255, 255, 204, .6);
}

h3{
    font-weight: normal;
    font-size: 1.25em;
}

abbr[title] {
    text-decoration: underline;
    -webkit-text-decoration: underline dotted;
    text-decoration: underline dotted;
    cursor: help;
    border-bottom: 0
}
img {
    max-width: 100%;
    height:auto;
    display: block;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    background-color: #006632;
    
    color: #ffc;
    font-size: 100%;
    line-height: 1.4;
    margin: 0;
    display:flex;
    flex-direction: column;
    min-height: 100vh;
    height:100%;
}

@media screen and (min-width:1024px) {
    body{
        background-image: url('../images/background.jpg');
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: cover;
    }
    .avif body{
        background-image: url('../images/background.avif');
    }
}



h1 {
    font-size: 2em;
    margin: .67em 0
}

abbr[title] {
    -webkit-text-decoration: underline dotted;
    text-decoration: underline dotted
}

b,
strong {
    font-weight: bolder
}

small {
    font-size: 80%
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline
}

sub {
    bottom: -.25em
}

sup {
    top: -.5em
}

.contact,.header, .nav, .main, .footer {
    background-color: rgba(62, 123, 62, 1);
    max-width: 1024px;
    margin: 0 auto;
    box-shadow: var(--page-block-shadow);
    width:100%;
}

.contact, .main, .footer{
    padding-inline: var(--page-inline-padding);
}

.contact {
    background-color: #006632;
    display: flex;
    justify-content: space-between;
}

.contact a{
    padding-block: .5em;
    text-decoration: none;
}

.contact a:hover{
    text-decoration: underline;
}

.header {
    background: url('../images/header1.jpg') 0 100% no-repeat;
    min-height: 229px;
    display: flex;
    align-items: center
}

.avif .header{
    background: url('../images/header1.avif') 0 100% no-repeat;
}

.hgroup {
    width: 340px;
    padding: .5rem;
    text-align: center;
    background-color: rgba(0, 0, 0, .3);
    text-shadow: 1px 1px 0 #000
}

.title {
    margin-bottom: 0;
    line-height: 1;
    font-size: 2.3rem
}

a:focus{
    outline:none;
}

a:focus-visible{
    outline: max(1px, 0.1em) solid currentColor;
    outline-offset: -0.1em;
}

.nav {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    background-color: #006632;
    padding: 0 calc(var(--page-inline-padding) - 1em);
}

.nav a {
    text-decoration: none;
    padding: .5rem 1rem;
    display: block;
    font-variant: small-caps
}

[aria-current="page"]{
	color: #fff;
    background-color: hsl(149.4, 50%, 20%);;
}

.footer,
.main {
    padding-block: var(--page-inline-padding); /*same as inline*/
}

.main{
    flex:1;
    display:flow-root;
}

.footer {
    text-align: center;
    border-top: 1px solid #006632
}

.my-email {
    letter-spacing: 0
}

.my-email::before {
    content: attr(data-user) "@"
}

.my-email:after {
    content: attr(data-domain)
}

h1,
h2 {
    font-family: Georgia, Times, Times New Roman, serif;
    font-weight: 400
}

h2 {
    text-shadow: 1px 0 0 rgba(0, 0, 0, .6)
}

a {
    color: #ffc
}

.fr {
    display: block;
    margin: 0 auto 1rem;
    max-width: 100%
}

@media screen and (min-width:480px) {
    body {
        font-size: 125%
    }
}

@media screen and (min-width:700px) {
    .fr {
        float: right;
        margin: 0 0 1rem 1rem;
        clear: right;
        box-shadow: 3px 3px 7px rgba(0, 0, 0, .4)
    }
}

